@charset "UTF-8";
.intro {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--light-color);
}
@media (max-width: 990px) {
  .intro {
    margin-bottom: 140px;
  }
}
.intro:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
  z-index: 2;
}
.intro__inner {
  position: absolute;
  z-index: 3;
  transform: translateY(-50%);
  top: 50%;
}
@media (max-width: var(--container-width)) {
  .intro__inner {
    margin-right: 42px;
  }
}
@media (max-width: 990px) {
  .intro__inner {
    margin-right: 20px;
  }
}
.intro__head {
  bottom: 60vh;
}
@media (max-width: 990px) {
  .intro__head {
    position: absolute;
  }
}
.intro__title {
  font-family: "Orbitron", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 112px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--secondary-color);
}
@media (max-width: 1330px) {
  .intro__title {
    font-size: 70px;
  }
}
@media (max-width: 990px) {
  .intro__title {
    font-size: 40px;
    line-height: 140%;
  }
}
.intro__subtitle {
  font-weight: 700;
  font-size: 46px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  margin-bottom: 30px;
}
@media (max-width: 1330px) {
  .intro__subtitle {
    font-size: 28px;
  }
}
@media (max-width: 990px) {
  .intro__subtitle {
    font-size: 16px;
  }
}
.intro__content {
  background: url("../assets/img/info-bg.png") center no-repeat;
  width: 650px;
  height: 254px;
  display: flex;
}
@media (max-width: 1330px) {
  .intro__content {
    background: url("../assets/img/info-bg-1330.png") center no-repeat;
    width: 421px;
    height: 187px;
  }
}
@media (max-width: 990px) {
  .intro__content {
    width: 100%;
    height: 100%;
    background: var(--primary-color);
    margin-top: 100vh;
    border-bottom: 5px solid var(--secondary-color);
  }
}
@media (max-width: 990px) {
  .intro__content:after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    right: 0;
    width: 55%;
    height: 10px;
    background-color: white;
    clip-path: polygon(0 0, 100% 0, 96% 100%, 0 100%);
  }
  .intro__content:before {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: -5px;
    right: 0;
    width: 55%;
    height: 5px;
    background-color: var(--primary-color);
    clip-path: polygon(2% 0, 100% 0, 100% 100%, 0 100%);
  }
}
.intro__info {
  padding: 24px 24px 27px 35px;
}
@media (max-width: 1330px) {
  .intro__info {
    padding: 16px 22px;
  }
}
@media (max-width: 990px) {
  .intro__info {
    padding: 17px 20px;
  }
}
.intro__info p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  margin-bottom: 5px;
  width: 500px;
}
@media (max-width: 1330px) {
  .intro__info p {
    font-size: 12px;
    width: 326px;
  }
}
@media (max-width: 990px) {
  .intro__info p {
    font-size: 11px;
    line-height: 140%;
    width: 100%;
  }
}
.intro__info a {
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--light-color);
  padding-right: 10px;
  position: relative;
}
@media (max-width: 1330px) {
  .intro__info a {
    font-size: 12px;
  }
}
.intro__info a .read__more {
  fill: var(--light-color);
}

.catalog {
  padding-bottom: 60px;
  background-color: var(--light-color);
}
.catalog__materials {
  margin: 50px 0 0 0;
}
.catalog__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -10px;
}
.catalog__item {
  position: relative;
  padding: 0 10px;
  margin-bottom: 60px;
}
@media (max-width: 1330px) {
  .catalog__item {
    margin-bottom: 45px;
  }
}
.catalog__text {
  padding-top: 10px;
  width: 100%;
  height: 55px;
  font-weight: 900;
  font-size: 20px;
  line-height: 120%;
  display: flex;
  align-items: flex-start;
  text-align: center;
  justify-content: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--text-color-light);
  position: absolute;
  left: 50%;
  bottom: 45px;
  transform: translate(-50%, 0);
}
