  .catalog__text {
    font-size: 16px;
    line-height: 130%;
  }
}
@media (max-width: 767px) {
  .catalog__text {
    height: 45px;
  }
}
.catalog__text:after {
  content: "";
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: var(--secondary-color);
  bottom: -13px;
  left: 50%;
  transform: translate(-50%, 0);
}
