    height: 1px;
  }
}
.catalog .catalog__figure {
  position: relative;
  z-index: 1;
  width: 190px;
  height: 233px;
  background-color: var(--secondary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
