    width: 140px;
    height: 203px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
@media (max-width: 767px) {
  .catalog .catalog__figure {
    width: 130px;
    height: 154px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
.catalog .box {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 184px;
  height: 227px;
  background-color: var(--primary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .box {
    width: 136px;
    height: 199px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
@media (max-width: 767px) {
  .catalog .box {
    width: 126px;
    height: 150px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
.catalog .catalog__figure-material {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -100%);
  z-index: 3;
}
@media (max-width: 1330px) {
  .catalog .catalog__figure-material {
    top: 45%;
  }
}
.catalog .catalog__image-border-1 {
  position: relative;
  z-index: 4;
  width: 140px;
  height: 162px;
  background-color: var(--secondary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .catalog__image-border-1 {
    width: 102px;
    height: 118px;
  }
}
@media (max-width: 767px) {
  .catalog .catalog__image-border-1 {
    width: 80px;
    height: 93px;
  }
}
.catalog .catalog__image-border-2 {
  position: absolute;
  z-index: 5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 134px;
  height: 156px;
  background-color: white;
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .catalog__image-border-2 {
    width: 98px;
    height: 114px;
  }
}
@media (max-width: 767px) {
  .catalog .catalog__image-border-2 {
    width: 76px;
    height: 89px;
  }
}
.catalog .box-image {
  position: absolute;
  z-index: 6;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 128px;
  height: 150px;
  background-color: var(--primary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .box-image {
    width: 94px;
    height: 110px;
  }
}
@media (max-width: 767px) {
  .catalog .box-image {
    width: 72px;
    height: 85px;
  }
}
.catalog .box-image img, .catalog .box-image source {
  width: 128px;
  height: 150px;
  object-fit: cover;
}
@media (max-width: 1330px) {
  .catalog .box-image img, .catalog .box-image source {
    width: 94px;
    height: 110px;
  }
}
@media (max-width: 767px) {
  .catalog .box-image img, .catalog .box-image source {
    width: 72px;
    height: 85px;
  }
}
.catalog__button {
