  height: 101px;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}
.contacts__head:before {
  content: "";
  position: absolute;
  background-color: var(--light-color);
  opacity: 0.9;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.contacts__content {
  padding: 0 20px;
}
.contacts__title {
  font-weight: 900;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--text-color-dark);
  opacity: 0.5;
}
.contacts__subtitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-dark);
  opacity: 0.5;
}
.contacts__body {
  width: 321px;
  height: 225px;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}
.contacts__body:before {
  content: "";
  position: absolute;
  background-color: #fff;
  opacity: 0.9;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.contacts__info {
  display: flex;
  align-items: center;
  margin-bottom: 22px;
}
.contacts__info-inner {
  display: flex;
  flex-direction: column;
}
.contacts a,
.contacts span {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: var(--text-color-dark);
  opacity: 0.5;
}
.contacts__icon {
  width: 20px;
  margin-right: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.contacts__icon .contacts__location,
.contacts__icon .contacts__phone,
.contacts__icon .contacts__email {
  opacity: 0.5;
}
.contacts__tabs {
  padding-top: 300px;
}
.contacts__tabs .tabs__nav-btn--active .contacts__head:before {
  background-color: var(--secondary-color);
  opacity: 1;
}
.contacts__tabs .tabs__nav-btn--active .contacts__title,
.contacts__tabs .tabs__nav-btn--active .contacts__subtitle {
  color: #fff;
  opacity: 1;
}
.contacts__tabs .tabs__nav-btn--active .contacts__body:before {
  opacity: 1;
}
.contacts__tabs .tabs__nav-btn--active a,
.contacts__tabs .tabs__nav-btn--active span {
  color: var(--text-color-dark);
  opacity: 1;
}
.contacts__tabs .tabs__nav-btn--active .contacts__location,
.contacts__tabs .tabs__nav-btn--active .contacts__phone,
.contacts__tabs .tabs__nav-btn--active .contacts__email {
  opacity: 1;
