}
.contacts__tabs .tabs__content {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.contacts__tabs .tabs__content .map {
  width: 100%;
  height: 100%;
}
.contacts__tabs .tabs__content .map img {
  height: 100%;
  object-fit: cover;
}
.contacts__tabs .tabs__panel {
  display: none;
  height: 686px;
}
.contacts__tabs .tabs__panel .border-1 {
  border: 2px solid #acfab4;
}
.contacts__tabs .tabs__panel .border-2 {
  border: 2px solid #acb6fa;
}
.contacts__tabs .tabs__panel .border-3 {
  border: 2px solid #f18aaa;
}
.contacts__tabs .tabs__panel .border-4 {
  border: 2px solid #fae978;
}
.contacts__tabs .tabs__panel--active {
  display: block;
}